.home {
    margin: 186px auto 180px 175px;
}
._home{
    display: flex;
    gap: 82px;
}
.gif__img{
    width: 391px;
    height: 391px;
}
.previve{
    max-width: 800px;

}
.home__sub-title{
    color: rgba(255, 255, 255, 0.50);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 35px;
}
.home__title{
    margin-top: 24px;
    color: #FFF;
    font-family: 'Poppins';
    font-size: 70px;
    font-style: normal;
    font-weight: 700;
    line-height: 35px; 
}
.home__text{
    min-width: 490px;
    margin-top: 20px;
    color: #FFF;
    font-family: 'Poppins';
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
}
.home__list{
    display: flex;
    gap: 15px;
    margin-top: 40px;
    margin-left: 10px;
}
.home__link{
    padding: 15px 30px;
    border: 1px solid #007CED;
    border-radius: 30px;
    color: #FFF;
    text-transform: uppercase;
}
.home__link:hover{
    background-color: #615e5e;
}

@media (max-width: 1335px) {
.home{
    margin-left: -8px; 
}
}
@media (max-width: 1062px) {
    ._home {
        display: block;
    }
    .gif__img{
        margin: 0 auto;
    }
    .previve{
        margin: 0 auto;
    }
}
@media (max-width: 600px) {
    .home__title{
        font-size: 45px;
        white-space: nowrap;
    }
    .home__text{
        font-size: 12px;
        min-width: 0 !important;
    }

}