.contacts__title {
    display: block;
    padding: 50px 0 50px 150px;
    text-transform: uppercase;
    color: #ffffff;
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    border-bottom: 2px solid #615e5e;
    background: #252525;
    overflow: hidden;
}
.contacts__main{
    width: 100%;
    min-height: 550px;
    display: flex;
}
.contacts__img {
    width: 100px;
    margin: 200px 150px;
}