body {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    font-family: 'Poppins', sans-serif;
    background: #222;
    color: #fff;
    
   
}
.container {
    max-width: 1800px;
    width: 100%;
    margin: 0 auto;
    padding: 0 60px;
    
}
.main {
    min-height: 100%;
    flex: 1 0 auto;
}

.footer {
    display: block;
    flex: 0 0 auto;
}
