


.skills__title {
    display: block;
    padding: 50px 0;
    padding-left: 150px;
    color: #FFF;
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    text-transform: uppercase;
    background: #252525;
    border-bottom: 2px solid #615e5e;
}
.skills__wrapper{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    gap: 120px;
    margin-top: 15px;
    padding-left: 30px;
    max-width: 100%;
}

.scills__desc{
    max-width: 850px;
}
.skills__sub-title{
    max-width: 156px;
    color: #FFF;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    border-bottom: 1px solid #007CED;
    white-space: nowrap;
}
.scills__description{
    position: relative;
    margin-top: 20px;
    margin-left: 50px;
}
.scills__description::before:nth-child(1){
    content: '';
    position: absolute;
    top: 0px;
    left: -75px;
    width: 50px;
    height: 50px;
    background-image: url('../../img/svg1.svg');
}

.scills__desc .scills__description:nth-child(2)::before{
    content: '';
    position: absolute;
    top: 0px;
    left: -75px;
    width: 50px;
    height: 50px;
    background-image: url('../../img/svg1.svg');
}
.scills__desc .scills__description:nth-child(3)::before{
    content: '';
    position: absolute;
    top: 0px;
    left: -75px;
    width: 50px;
    height: 50px;
    background-image: url('../../img/svg2.svg');
}
.scills__desc .scills__description:nth-child(4)::before{
    content: '';
    position: absolute;
    top: 0px;
    left: -75px;
    width: 50px;
    height: 50px;
    background-image: url('../../img/svg3.svg');
}
.discription__title{
    color: #FFF;
    font-size: 18px;
    font-style: normal;
    font-weight: 700; 
}
.dicription__text{
    color: rgba(255, 255, 255, 0.70);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
}
.skills__chart{
    width: 850px;
}
.chatr__tittle{
    display: block;
    max-width: 190px;
    color: #FFF;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    white-space: nowrap;
    border-bottom: 1px solid #007CED;
}
.charts__skills{
    margin-top: 50px;
    display: flex;
    gap: 25px;
}
.charts__skills label {
    white-space: nowrap;
}
.charts__skills progress{
    width: 400px;
    margin-top: 5px;
    border: 1px solid #615e5e;
    border-radius: 7px;
}
.charts__skills progress::-webkit-progress-bar{
    background-color: #222;
}
.charts__skills progress::-webkit-progress-value{
    background-color: #007CED;
}
.charts__skills:nth-child(2) progress {
    margin-left: 50px;
}
.charts__skills:nth-child(3) progress {
    margin-left: 60px;
}
.charts__skills:nth-child(4) progress {
    margin-left: 7px;
}
.charts__skills:nth-child(5) progress {
    margin-left: 50px;
}
.charts__skills:nth-child(6) progress {
    margin-left: 60px;
}
.charts__skills:nth-child(7) progress {
    margin-left: 60px;
}