
._footer{
    margin-bottom: 0;
    padding-left: 60px;
    border-top: 2px solid #615e5e;
    width: 100%;
    max-width: 1800px;
}
.footer-nav__item{
    display: flex;
    gap: 40px;
    margin-top: 30px;
}
.footer-nav__links{
    color: #fff;
    text-transform: uppercase;
}
.footer-nav__links:hover{
    color: #007CED;
    transition: 0.5s;
    cursor: pointer;
}

