.projects__title{
    display: block;
    padding: 50px 0 50px 150px;
    text-transform: uppercase;
    color: #FFF;
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    border-bottom: 2px solid #615e5e;
    background: #252525;
    overflow: hidden;
}
.project__gallery{
    margin: 30px 0 30px 50px;
    max-width: 110px;
    color: #FFF;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    border-bottom: 1px solid #007CED;
}
.gallery__list{
    min-height: 450px;
    position: relative;
    width: 100%;
    max-width: 1800px;
}
.gallery__list{
    display: flex;
    gap: 50px;
    margin-left: 100px;

}
.gallery__img{
    position: relative;
    max-width: 350px;
    height: 250px;
}
.gallery__img:hover{
    opacity: 0.7;
}
.gallery__link p:hover{
    color: #007CED;
}
.gallery__text{
    max-width: 350px;
    text-align: center;
    color: #FFF;
    background-color: rgba(255, 255, 255, 0.3);
    fill: #007CED;
}
.gallery__item:nth-child(2) p {
    width: 325px;
}
