.header {
    max-width: 1800px;
    max-height: 162px;
    border-bottom: 2px solid #615e5e;

}
._header{
    display: flex;
    justify-content: space-between;
}
.logo {
    margin: 15px 0px;
    width: 80px;
}
.header__nav{
    margin-top: 58px;
}
.nav__list{
    display: flex;
    gap: 40px;
}
.nav__link{
    color: #fff;
}
.nav__link:hover{
    color: #007CED;
    transition: 0.5s;
    cursor: pointer;
}
.nav__link.active{
    border-bottom: 2px solid #007CED;
}
 @media (max-width: 560px) {
    .nav__list {
        flex-wrap: wrap;
    }
    .nav__item{
        flex: 2;
        width: 50% !important;
    }
    
 }